.App{
  display: flex;
  align-items: center;
  flex-direction: column;
  background-color: aquamarine;
  width: 400px;
}
.add_task{
  display: flex;
  align-items: center;
}
.add_task > .add_button {
  font-size: 25px;
  background-color: #64dd17;
  border-radius: 20px;
  border: none;
}
.add_button:hover,.delete_button:hover {
  cursor: pointer;
}
.list_task{
  padding-top: 5px;
  padding-bottom: 5px;
  display: flex;
  flex-direction: column;
  gap: 5px;
}
.task {
  display: flex;
  justify-content: space-between;
  background-color: azure;
  width: 300px;
}
.task > .delete_button { 
  background-color: #f44336;
  border-radius: 20px;
  border: none;
  font-size: 25px;

}
