.container{
  display: flex;
  height: 313px;
  width: 313px;
  flex-wrap: wrap;
  background-color: antiquewhite;
  gap: 10px;
}
.element{
  display: flex;
  justify-content: center;
  align-items: center;
  height: 90px;
  width: 90px;
  color: aliceblue;
  background-color: red;
  font-size: xx-large;
}
.element:hover {
  cursor: pointer;
}